import React, { FC, useEffect, useMemo, useRef, useState } from 'react';
import {
  GeneralSearchContainer,
  GeneralSearchWrapper,
  SearchIconContainer,
  SearchResultsContainer,
  StyledGeneralSearch,
} from './styled';
import SearchIcon from '../Icons/SearchIcon/SearchIcon';
import useOnClickOutside from '../../../hooks/useClickOutside';
import algoliasearch from 'algoliasearch/lite';
import { InstantSearch } from 'react-instantsearch';
import { SearchBox } from './SearchBox';
import SearchResult from './SearchResult';
import useWindowSize, { isDesktop } from '../../../hooks/useWindowSize';
import { MultipleQueriesQuery } from '@algolia/client-search';

type Props = {
  isSearchActive?: boolean;
  setIsSearchActive?: (value: boolean) => void;
};

export const GeneralSearch: FC<Props> = ({ setIsSearchActive }) => {
  const [isSearchInputVisible, setIsSearchInputVisible] = useState(false);

  const [searchValue, setSearchValue] = useState('');
  const handleSearchClick = () => {
    setIsSearchInputVisible(true);
    setIsSearchActive?.(true);
    if (!isSearchInputVisible)
      setTimeout(() => {
        (document.querySelector('#general-search-input') as HTMLInputElement)?.focus();
      }, 0);
  };

  const windowSize = useWindowSize();
  const isDesktopScreen = isDesktop(windowSize.width as number);

  const searchRef = useRef(null);
  useOnClickOutside(searchRef, () => {
    setIsSearchInputVisible(false);
    setSearchValue('');
  });

  const algoliaClient = useMemo(
    () =>
      algoliasearch(
        process.env.GATSBY_ALGOLIA_APP_ID ?? '',
        process.env.GATSBY_ALGOLIA_SEARCH_KEY ?? '',
      ),
    [],
  );

  const searchClient = {
    ...algoliaClient,
    search(requests: MultipleQueriesQuery[]) {
      if (requests.every(({ params }) => !params?.query)) {
        return Promise.resolve({
          results: requests.map(() => ({
            hits: [],
            nbHits: 0,
            nbPages: 0,
            page: 0,
            processingTimeMS: 0,
            hitsPerPage: 0,
            exhaustiveNbHits: false,
            query: '',
            params: '',
          })),
        });
      }

      return algoliaClient.search(requests);
    },
  };

  useEffect(() => {
    if (!isSearchInputVisible) {
      setIsSearchActive?.(false);
    }
  }, [isSearchInputVisible]);

  useEffect(() => {
    setIsSearchActive?.(false);
    setIsSearchInputVisible(false);
    setSearchValue('');
  }, [isDesktopScreen]);

  return (
    <GeneralSearchWrapper ref={searchRef}>
      <InstantSearch searchClient={searchClient} indexName={'news'}>
        <GeneralSearchContainer isSearchInputVisible={isSearchInputVisible}>
          <StyledGeneralSearch>
            {!isSearchInputVisible && (
              <SearchIconContainer onClick={handleSearchClick}>
                <SearchIcon></SearchIcon>
              </SearchIconContainer>
            )}
            <SearchBox
              isSearchInputVisible={isSearchInputVisible}
              setSearchValue={setSearchValue}
              searchValue={searchValue}
              setIsSearchInputVisible={setIsSearchInputVisible}
            ></SearchBox>
            {!!searchValue && (
              <SearchResultsContainer
                initial={{ width: 0, opacity: 0 }}
                animate={{ width: isDesktopScreen ? 425 : 375, opacity: 1 }}
              >
                <SearchResult />
              </SearchResultsContainer>
            )}
          </StyledGeneralSearch>
        </GeneralSearchContainer>
      </InstantSearch>
    </GeneralSearchWrapper>
  );
};
