import React, { FC } from 'react';
import { Configure, Highlight, Hits, Index, PoweredBy } from 'react-instantsearch';
import { ResultsByIndex, StyledPowerdBy, StyledSearchResults } from './styled';
import { Text } from '../Text';
import { AlgoliaIndexes } from '../../constants';
import type { Hit } from 'instantsearch.js';
import { Link } from 'gatsby';

type TCustomHit = Hit & {
  title: string;
  slug: string;
};

type PageHitProps = {
  hit: TCustomHit;
  indexName: AlgoliaIndexes;
};

const getUrl = (slug: string, indexName: AlgoliaIndexes) => {
  switch (indexName) {
    case AlgoliaIndexes.Terms: {
      return `/glossary/${slug}`;
    }

    case AlgoliaIndexes.StaticPages: {
      return slug;
    }

    default: {
      return `/${indexName}/${slug}`;
    }
  };
};

const PageHit: FC<PageHitProps> = ({ hit, indexName }) => (
  <Link to={getUrl(hit.slug, indexName)} key={hit.slug}>
    <Text.Regular.Normal.sm>
      <Highlight attribute="title" hit={hit} />
    </Text.Regular.Normal.sm>
  </Link>
);

const HitsInIndex = ({ indexName }: { indexName: AlgoliaIndexes }) => (
  <Index indexName={indexName}>
    <Configure hitsPerPage={5} />
    <Hits
      className="Hits"
      hitComponent={(props: { hit: TCustomHit }) => (
        <PageHit {...props} indexName={indexName} />
      )}
    />
  </Index>
);

const SearchResult = () => (
  <StyledSearchResults>
    <ResultsByIndex>
      {Object.values(AlgoliaIndexes).map((indexName) => (
        <HitsInIndex indexName={indexName} key={indexName} />
      ))}
    </ResultsByIndex>
    <StyledPowerdBy>
      <PoweredBy />
    </StyledPowerdBy>
  </StyledSearchResults>
);

export default SearchResult;
